<style scoped>
  .ll-p-m-w {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
  }
  .sf-pre-goods-img-w {
    width: 110px;
    height: 60px;
  }
  .sf-pre-goods-img-w>img {
    width: 110px;
    height: 60px;
  }
  .ll-p-m-c-r {
    padding: 0 10px;
  }
  .ll-erp-p-no {
    color: #666;
    font-size: 12px;
  }
</style>
<template>
  <div class="add-form-wraper">
    <el-form ref="form" :model="detailMsg" label-width="160px">
      <el-form-item label="门店/团长">
        <div v-if="detailMsg.storeLeaderId">
          {{detailMsg.storeNo}}_{{storeName}} <span style="color: green; font-weight: bold;">/</span> {{detailMsg.storeLeaderId}}_{{virtualStoreName}}_{{userName}}
        </div>
        <div v-else>
          {{detailMsg.storeNo}}_{{storeName}}
        </div>
      </el-form-item>
      <el-form-item label="群接龙名称">
        <div>{{detailMsg.id}}_{{detailMsg.activityName}}</div>
      </el-form-item>
      <el-form-item label="群接龙类型">
        <div>{{detailMsg.activityType === 1 ? '预售群接龙' : detailMsg.activityType === 2 ? '及时达群接龙' : '-'}}</div>
      </el-form-item>
      <el-form-item label="群接龙状态">
        <div>{{formatActivityStatus(detailMsg.activityStatus)}}</div>
      </el-form-item>
      <el-form-item label="开始时间">
        <div>{{detailMsg.activityStartTime}}</div>
      </el-form-item>
      <el-form-item label="结束时间">
        <div>{{detailMsg.activityEndTime}}</div>
      </el-form-item>
      <el-form-item label="提货日期" v-if="detailMsg.activityType === 1">
        <div v-if="detailMsg.pickUpTime">{{detailMsg.pickUpTime.substr(0, 10)}}</div>
      </el-form-item>
      <el-form-item label="设置活动商品">
        <el-table
          v-if="detailMsg.goodsDetailVoList && detailMsg.goodsDetailVoList.length"
          class="fixed-height choose-goods-wrap"
          :data="detailMsg.goodsDetailVoList"
          empty-text="暂无选中任何商品"
          style="width:100%;"
          max-height="560"
          size="small">
          <el-table-column min-width="300px" label="商品信息">
            <template slot-scope="scope">
              <div class="ll-p-m-w">
                <div class="sf-pre-goods-img-w">
                  <img v-if="scope.row.imgUrl && scope.row.imgUrl != ''" :src="scope.row.imgUrl" alt="" onerror="onerror=null;src='/shop/static/img_error.png'">
                  <img v-else src="/shop/static/img_error.png" alt="">
                </div>
                <div class="ll-p-m-c-r">
                  <div>
                    {{scope.row.goodsName}}
                  </div>
                  <div class="ll-erp-p-no">SKU编码：{{scope.row.goodsNo || '--'}}</div>
                  <div class="ll-erp-p-no" v-if="scope.row.id">商品ID：{{scope.row.id || '--'}}</div>
                </div>
              </div>
            </template>
          </el-table-column>
          <el-table-column min-width="100px" prop="spec" label="规格"></el-table-column>
          <el-table-column min-width="100px" prop="price" label="原价"></el-table-column>
          <el-table-column min-width="100px" prop="groupPrice" label="活动价"></el-table-column>
          <el-table-column min-width="100px" prop="discount" label="折扣" v-if="detailMsg.activityType === 2"></el-table-column>
          <el-table-column min-width="100px" prop="curStock" label="门店库存"  v-if="detailMsg.activityType === 2"></el-table-column>
          <el-table-column min-width="100px" prop="goodsActivityTypeName" label="活动类型" v-if="detailMsg.activityType === 1"></el-table-column>
          <el-table-column min-width="100px" prop="goodsActivityId" label="活动id" v-if="detailMsg.activityType === 1"></el-table-column>
        </el-table>
      </el-form-item>
      <hr class="s-slip-tb">
      <el-form-item>
        <el-button size="small" @click="doCancelHandel">返回</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import requestUrl from '@/api/Order'
  export default {
    data() {
      return {
        id: '',
        storeName: '',
        virtualStoreName: '',
        userName: '',
        detailMsg: {},
        activityStatusOptions: [
          {
            label: '全部',
            value: ''
          },
          {
            label: '未开始',
            value: 2
          },
          {
            label: '已发布',
            value: 1
          },
          {
            label: '已终止',
            value: 5
          },
          {
            label: '进行中',
            value: 3
          },
          {
            label: '已结束',
            value: 4
          }
        ]
      }
    },
    created() {
      this.id = this.$route.params.id
      this.storeName = this.$route.query.storeName
      this.virtualStoreName = this.$route.query.virtualStoreName
      this.userName = this.$route.query.userName
      this.getDetailMsg()
    },
    methods: {
      getDetailMsg() {
        utils.showLoading()
        this.$http.post(requestUrl.viewShopSolitaireActivity, {id: this.id, DIFFCITY: true}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              this.detailMsg = {...res.data}
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      formatActivityStatus(status) {
        let filterMsg = this.activityStatusOptions.filter(item => {
          return item.value === status
        })[0]
        if(filterMsg) {
          return filterMsg.label
        }else {
          return status
        }
      },
      doCancelHandel() {
        this.$router.push('/solitaire_order')
      }
    },
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }
</script>


